@import url('https://fonts.googleapis.com/css?family=Audiowide');
@import url('https://fonts.googleapis.com/css?family=Orbitron');
.warningMarquee {
    display: flex;
    position:absolute;
    font-family: "Orbitron", sans-serif;
    top: 38%;
    left: 10%;
    font-size: 2rem;
    z-index: -1;
    color: red;
}
.bars {
    display: flex;
    position:absolute;
    top: 0%;
    font-size: 2rem;
}

.warningvisible{
    pointer-events: none;
    display: flex;
    position: fixed;
    animation: rotateWarning 2s, flashWarning 3s alternate infinite;
    opacity: .65;
    transition:2s;
}

.warninginvisible{
    pointer-events: none;
    display: flex;
    position: fixed;
    top:0%;
    opacity: 0;
    transition: 2s;
}

@keyframes rotateWarning{
    0%{
        transform:rotate3d(1,0,0,0deg);
    }
    100%{
        transform:rotate3d(1,0,0,1080deg);
    }
}

@keyframes flashWarning{
    0%{
        opacity: 0.05;
    }
    100%{
        opacity: 0.25;
    }
}

