.eyevisible{
    pointer-events: none;
    display: flex;
    position: fixed;
    opacity: 1;
    animation: moveEyes 5s alternate infinite, openEyes 1.5s alternate-reverse infinite;
    
}

.eyeinvisible{
    pointer-events: none;
    display: flex;
    position: fixed;
    top: 0%;
    opacity: 0;
}

@keyframes moveEyes{
    from{
        transform: translate(-20px);
    }
    to{
        transform: translate(20px);
    }
}

@keyframes openEyes{
    0%{
        opacity: 0;
    }
    70%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}