* {
  box-sizing: border-box;
}

body {
  margin:0;
}



.nav {
  background-color: black;
  color: white;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  cursor: pointer;
}

.navbar-title {
  font-size: 2rem;
  justify-content: center;
}

.textdiv {
  text-align: right;
  grid-column: 1/2;
  grid-row: 1/2;
  margin-left: 10vmin;
  line-height: 2;
  font-size: 1.25em;
}
.List{
  grid-column: 2/3;
  grid-row: 2/3;
}

.nav ul {
  padding: 0;
  margin: 0;
  display: block;
  list-style: none;
  gap: 1rem;
}

.nav li {
  max-width: max-content;
  overflow: hidden;
  transition: .2s;
  
}
main .nav li{
  border: 2px solid gray;
  border-radius: 1em;
  
}
.nav li:hover {
  background-color: #777;
  transform: scale(1.5);
}



.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.ListTextVis{
  cursor: default;
  height:0;
  width: 0;
  display: none;
  transition: 2s;
}

.ListTextHide{
  cursor: default;
  display: block;
  transition: 2s;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
}