.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#navbar{
  flex-direction: row;
  justify-content: space-between;
  background-color: rgb(32, 32, 32);
  color: white;
  display:block;
}
body{
  background-color: black;
}
main {
  word-spacing: 5px;
  background-color: black;
  color: white;
}
section{
  padding: 20vmin;
}
#contact{
  text-align: center;
  justify-content: center;
  gap: 2rem;
  padding-bottom: 2rem;
}

footer {
  background-color: grey;
  color: white;
  text-align: center;
  justify-content: center;
}
